"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { cn } from "@/lib/utils";
import { Button } from "@/components/ui/button";
import { ScrollArea } from "@/components/ui/scroll-area";
import { getUser, logout } from "@/lib/utils/auth";
import { useRouter } from "next/navigation";

const routes = [
  {
    label: "Overview",
    href: "/dashboard",
    icon: "📊",
  },
  {
    label: "Inventory",
    href: "/dashboard/inventory",
    icon: "📦",
  },
  {
    label: "Booking",
    href: "/dashboard/booking",
    icon: "📝",
  },
  {
    label: "Reports",
    href: "/dashboard/reports",
    icon: "📈",
  },
  {
    label: "Orders",
    href: "/dashboard/orders",
    icon: "🛒",
  },
  {
    label: "Users",
    href: "/dashboard/users",
    icon: "👥",
  },
];

export function Sidebar() {
  const pathname = usePathname();
  const router = useRouter();
  const user = getUser();

  const handleLogout = () => {
    logout();
    router.push('/auth/signin');
  };

  return (
    <div className="space-y-4 py-4 flex flex-col h-full bg-gray-50 border-r">
      <div className="px-3 py-2">
        <h2 className="mb-2 px-4 text-lg font-semibold">
          Stock Management
        </h2>
        <div className="mb-4 px-4">
          <p className="text-sm text-gray-600">Welcome,</p>
          <p className="text-sm font-medium">{user?.name}</p>
          <p className="text-xs text-gray-500 capitalize">{user?.role}</p>
        </div>
      </div>
      <ScrollArea className="flex-1 px-3">
        {routes.map((route) => (
          <Link
            key={route.href}
            href={route.href}
            className={cn(
              "flex items-center gap-x-2 text-sm font-medium px-3 py-2 rounded-lg transition-colors",
              pathname === route.href
                ? "bg-black text-white"
                : "text-gray-500 hover:text-black hover:bg-gray-100"
            )}
          >
            <span className="w-6">{route.icon}</span>
            {route.label}
          </Link>
        ))}
      </ScrollArea>
      <div className="px-3 py-2">
        <Button
          onClick={handleLogout}
          variant="outline"
          className="w-full justify-start text-gray-500 hover:text-black"
        >
          <span className="w-6">🚪</span>
          Logout
        </Button>
      </div>
    </div>
  );
}
